.title-font(@font-size; @line-height) {
	.title-base();
	font-size: @font-size;
	line-height: @line-height;
}

.title {
	.title-base();
	.title-font(36px; 36px);
}

.title--style {
	.title-base();
	text-transform: uppercase;
}

.title--light {
	.title-base();
	.title-font(36px; 36px);
	color: @light-text;
	text-align: center;
}
